import { exFetch,exFetchWithoutToken,exFetchWithoutTokenFormData } from "../index.js"
import {listToTree,deepClone, isNotNil} from '@/lib/lib.js'

const BaseUrl = 'https://api-njdm-pv.saicmotor.com/adcdataplat'

export const login = async (params={}) => { // 
	try{
		const res = await exFetchWithoutTokenFormData(`${BaseUrl}/sys/sys-management/login/`,params,'POST')
		res.isOk = true
		return res
	}catch(err){
		err.isOk=false
		console.log(err)
		return err
	}
}

export const getPublicKey = async (params={}) => { // 
	try{
		const res = await exFetchWithoutToken(`${BaseUrl}/sys/sys-management/login/`,params,'GET')
		res.isOk=true
		return {
			isOk:true,
			...res.data
		}
	}catch(err){
		err.isOk=false
		console.log(err,'getPublicKey')
		return err
	}
}

