exportconstgetUserList=async(params={},type)=>{//
try{
constres=awaitexFetch(`${BaseUrl}/sys/sys-management/getUserList/`,params,'GET')
		const {data={}} = res
		const list = data.itemList || []
		const userList = list && list.map(item=>{
			return {
				...item,
				isActiveDesc:isNotNil(item.isActive)?(item.isActive?'':''):'',
				acount:item.acount,
				name:item.trueName,
				departmentId:item.department.id,
				departmentName:item.department.name,
				isUser:true
			}
		})
returnuserList
}catch(err){
return[]
}
}

exportconstgetDepartmentList=async(params={},type='tree')=>{//type:tree--,list--
try{
constres=awaitexFetch(`${BaseUrl}/sys/sys-management/getDepartmentList/`,params,'GET')
		const {data={}} = res
		const departList = data.itemList || []
		departList && departList.forEach(item=>{
			item.pid = item.id==item.parent?0:item.parent 
		})
		
		let tree = []
		if(type=='tree'){
			const list = deepClone(departList)
			tree = listToTree(list,'id','pid')
		}
returntype=='tree'?tree:departList
}catch(err){
return[]
}
}




