/**
 *   ==========      ==========
 */

const calcSum = (data=[],key="",label="",isTime=false)=>{ // list:, key:,{2022-05-01:11,...}
	const res = {}
	const cateArr = []
	const list = data.filter(i=>i[key])
	list.forEach(j=>{
		cateArr.push(j.day)
		const val = isTime?(j[key]/3600):j[key]
		if(res[j.day]){
			res[j.day] = res[j.day] + val
		}else{
			res[j.day] = val
		}
	})
	const categories = uniqArr(cateArr)
	return {
		categories,
		series: [
			{
				name: label,
				color:"#6970e2",
				data: categories.map(i=>res[i].toFixed(2))
			}
		]
	}
}

export const getTDataDayRes = async(params={})=>{ // 
	// params:{terminalcode:'',starttime:'2021-11-24',endtime:'2021-11-25'}
	// starttime,endtime 
	try{
		const res = await map_fetch(`${BaseUrl}/icv/TDataDay/listDayStatistic`,params,'GET')
		let chartData = {}
		if(res.code==200){
			// 
			const list = (res.data||[]).filter(i=>!i.remark)
			
			// ,
			const distanceObj = calcSum(list,'sumdrivedistance','')
			
			// , sumdrivetime
			const sumdrivetimeObj = calcSum(list,'sumdrivetime','',true)
			
			// ,10
			const speedgreater10timeObj = calcSum(list,'speedgreater10time','10',true)
			
			// ,80
			const speedgreater80timeObj = calcSum(list,'speedgreater80time','80',true)
			// let res = {
			// 	categories: ["2016","2017","2018","2019","2020","2021"],
			// 	series: [
			// 		{
			// 			name: "",
			// 			data: [35,36,31,33,13,34]
			// 		},
			// 		{
			// 			name: "",
			// 			data: [18,27,21,24,6,28]
			// 		}
			// 	]
			// };
			chartData={
				distanceObj,
				sumdrivetimeObj,
				speedgreater10timeObj,
				speedgreater80timeObj
			}
		}
		
		return {
			chartData,
			isOk:true
		}
	}catch(err){
		err.isOk=false
		console.log(err)
		return err
	}
}

