exportconstlistToTree=(list=[],idName='id',pidName='pid')=>{
letinfo=list.reduce((map,node)=>{
map[node[idName]]=node
returnmap
},{})
letres=list.filter(node=>{
if(info[node[pidName]]){
isNil(info[node[pidName]].children)&&(info[node[pidName]].children=[])
info[node[pidName]].children.push(node)
}
return!node[pidName]
})
returnres
}

//
exportconsttreeToList=(tree,result=[])=>{
tree.forEach(node=>{
result.push(node)
node.children&&treeToList(node.children,result)
})
constres=result.map(i=>{
deletei.children
returni
})
returnres
}