import { exFetch } from "../index.js"
import {orderStatusEnum} from '@/page_carBorrow/const.js'
import {isNotNil} from '@/lib/lib.js'

constexceptionTemp={errMessage:'',hasErrMessage:true}
const BaseUrl = 'https://api-njdm-pv.saicmotor.com/adcdataplat/sys/vehicle-management'

constcalcUrl=(params={},type='GET',str)=>{
	const method = type.toUpperCase()
constextra=method==='PUT'?(isNotNil(params.id)?params.id:''):''
return`${BaseUrl}/${str}/${extra}`
}

//-------------      -------------

exportconstgetVinOrderList=async(params={},type='GET')=>{//VIN(pageSize,pageNum)
try{
constres=awaitexFetch(calcUrl(params,type,'vehicle-borrow/current'),params,type)
res.isOk=true
		returnres
}catch(err){
return{isOk:false}
}
}

exportconstgetOrderListByParam=async(params={},type='GET')=>{//vinyearmonth
try{
constres=awaitexFetch(calcUrl(params,type,'vehicle-borrow/filter'),params,type)
res.isOk=true
		return{
			isOk: true,
			itemList:(res.data || {itemList:[]}).itemList,
			total:(res.data || {total:0}).total
		}
}catch(err){
return{isOk:false}
}
}

exportconstcreateBorrowOrder=async(params={},type='POST',errException={...exceptionTemp})=>{//
try{
constres=awaitexFetch(calcUrl(params,type,'vehicle-borrow/create'),params,type,errException)
res.isOk=true
		returnres
}catch(err){
return{isOk:false}
}
}

exportconstgetBorrowOrderList=async(params={},type='GET')=>{//
try{
constres=awaitexFetch(calcUrl(params,type,'vehicle-borrow/my-borrow'),params,type)
if(res.code===200){
res.data.itemList&&res.data.itemList.forEach(item=>{
item.latestOrderSatus=item.orderProcesses&&item.orderProcesses[0]&&item.orderProcesses[0].status?`${orderStatusEnum[item.orderProcesses[0].status]['desc']}`:'--'
item.status=item.orderProcesses&&item.orderProcesses[0]&&item.orderProcesses[0].status?item.orderProcesses[0].status:''
item.vin=item.vehicle && item.vehicle.vin? item.vehicle.vin:''
item.trueName=item.borrower && item.borrower.trueName? item.borrower.trueName:''
			})
}
		res.isOk = true
returnres
}catch(err){
return{isOk:false}
}
}

