exportconstgetVehicleProject=async(params={},type='get')=>{//
try{
constres=awaitexFetch(calcUrl(params,type,'vehicle-project'),params,type)
returnres?.data?.itemList||[]
}catch(err){
return[]
}
}

exportconstgetVehicleInfoList=async(params={},type='GET')=>{//(get)
try{
		constres=awaitexFetch(calcUrl(params,type,'vehicle-info'),params,type)
		
		if(res.code===200){
res.data && res.data.itemList&&res.data.itemList.forEach(item=>{
item.ownerId=item.owner.id
item.ownerDesc=item.owner.trueName||'--'
item.governingUserId=item.governingUser.id
item.governingUserDesc=item.governingUser.trueName||'--'
item.createUserDesc=item.createUser.trueName||'--'
item.lastModifyUserDesc=item.lastModifyUser.trueName||'--'
				item.isPublicDesc = isNotNil(item.isPublic)?(item.isPublic?'':''):'--'
			})
}
		res.isOk = true
returnres
}catch(err){
return{
isOk:false
}
}
}

exportconstupdateVehicleInfoList=async(params={},type='GET',isExFetch=false,errException={...exceptionTemp})=>{///(post)
consturl=calcUrl(params,type,'vehicle-info')
try{
constres=awaitexFetch(url,params,type,errException)
		res.isOk = true
returnres
}catch(err){
return{isOk:false}
}
}