export class MapMannager{
	static initMap(MAP_CTX,isJoinCluster=false,data=[]){
		MapMannager.calcMarkerList(isJoinCluster,data) // 1. this.Marker_List
		
		isJoinCluster && MapMannager.addCluster(MAP_CTX) // 2.
		
		MapMannager.addMarkers(this.Marker_List,MAP_CTX)  // 3.marker
	}
	
	static addCluster(MAP_CTX){ // 
	
		//  on.("markerClusterCreate", (e) => {})
		
		MAP_CTX.initMarkerCluster({
			enableDefaultStyle: false,
			zoomOnClick: true,
			gridSize: 40,
			complete(res) {
				console.log('initMarkerCluster', res)
			}
		});
			
		MAP_CTX.on("markerClusterCreate", (e) => { // enableDefaultStyle  true 
			console.log("markerClusterCreate", e);
			const clusters = e.clusters
			
			const clusterMarkers = clusters.map(cluster => {
				const {center,clusterId,markerIds} = cluster
				
				return {
					...center,
					width: 0,
					height: 0,
					clusterId, // 
					iconPath:'/static/mapImg/cluster.png',
					label: {
						content: markerIds.length+'',
						fontSize: 12,
						width: 45,
						height: 45,
						borderWidth: 4,
						borderColor: '#6970e2',
						bgColor: '#fff',
						borderRadius: 28,
						textAlign: 'center',
						// anchorX: 0,
						// anchorY: -18,
					}
				}
			})

			MAP_CTX.addMarkers({
				markers:clusterMarkers, // 
				clear: false,
				complete(res) {
					console.log('clusterCreate addClusterMarkers', res,clusterMarkers,'====clusterMarkers99999====')
				}
			})
		});
		
		MAP_CTX.on('markerClusterClick', res => {
			console.log('markerClusterClick', res)
		})
	}
	
	static addMarkers(Marker_List,MAP_CTX,isClear=false){ // marker
		MAP_CTX.addMarkers({ // 
			markers:Marker_List,
			clear: isClear,
			complete(res) {
				console.log('addMarkers111111111', res)
			}
		})
	}
	
	static calcMarkerList(isJoinCluster,Real_Time_Data_List){ // markerList
		let list = []
		Real_Time_Data_List.forEach((item,index)=>{
			list.push({
				id:index+1,
				width: 30,
				height: 30,
				joinCluster: isJoinCluster, // 
				iconPath:item.isonline?'/static/mapImg/carColor.png':'/static/mapImg/carGrey.png',
				latitude:item.lat,
				longitude:item.lon,
				label: {
					color: '#bbb',
					fontSize: 10,
					borderWidth: 0,
					borderRadius: 3,
					borderColor: '#000000',
					bgColor: '#ebeef11f',
					padding: 1,
					textAlign: 'center',
				},
				infoItem:{ // 
					...item
				}
			})
		})
		
		this.Marker_List = [...list]
		return [...list]
	}
	
	static getMarkerList(){
		return this.Marker_List
	}
}